const uni = require('uni').default;












import pageHead from '../../../components/page-head.vue'

export default {
	data() {
		return {
			title: 'navigateTo/Back, redirectTo'
		}
	},
	methods: {
		navigateTo: function () {
			uni.navigateTo({
				url: 'navigator'
			})
		},
		navigateBack: function () {
			uni.navigateBack()
		},
		redirectTo: function () {
			uni.redirectTo({
				url: 'navigator'
			})
		}
	},
	components: {
		pageHead

	}
}



